@import "../variables";

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  @media (max-width: 870px) {
    padding: 5px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 850px) {
      flex-direction: column;
      .header__cart {
        margin-bottom: 20px;
      }
      .header__logo {
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;
        text-align: center;
        img {
          margin: 0;
        }
        p {
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }
    }
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
